import Link from "next/link";
import Layout from "../components/Layout";

const SignUp = () => (
    <Layout>
        <form className="mt-5">
            <div className="form-group row d-flex  justify-content-center">
                <label htmlFor="firstName" className="col-sm-2">First Name</label>
                <div className="col-sm-10 col-md-6 col-lg-6">
                    <input type="text" className="form-control" id="firstName" required />
                </div>
            </div>

            <div className="form-group  row d-flex justify-content-center">
                <label htmlFor="lastName" className="col-sm-2">Last Name</label>
                <div className="col-sm-10 col-md-6 col-lg-6">
                    <input type="text" className="form-control" id="lastName" required />
                </div>
            </div>

            <div className="form-group row  d-flex justify-content-center">
                <label htmlFor="email" className="col-sm-2">Email</label>
                <div className="col-sm-10 col-md-6 col-lg-6">
                    <input type="email" className="form-control" id="email" required />
                </div>
            </div>

            <div className="form-group row  d-flex justify-content-center">
                <label htmlFor="password" className="col-sm-2">Password</label>
                <div className="col-sm-10 col-md-6 col-lg-6">
                    <input type="password" className="form-control" id="password" required />
                </div>
            </div>
            
            <button type="submit" className="btn btn-primary form-control w-25">Sign Up</button>
        </form>

        <p className="text-center">Already a member, <Link href="/login"><a>Login</a></Link>.</p>

        <style jsx>
            {`
                @media only screen and (max-width: 768px) {
                    
                }
            `}
        </style>
    </Layout>
)

export default SignUp;